import React, { useState } from 'react';
import './index.css';
import axios from 'axios';
import CartButton from './CartButton';

const SaucesCard = ({ _id, title, images, ingredients, price }) => {
  const [successMessages, setSuccessMessages] = useState([]);

  const addToCart = () => {
    const dataToSend = {
      productId: _id,
      productQuantity: 1,
      size: 150,
      extras: {},
    };
    
    axios
      .post('https://xfood-ob2l.onrender.com/add-to-cart', dataToSend, { withCredentials: true })
      .then((response) => {
        console.log('Product added to cart:', response.data);
        const newSuccessMessages = [...successMessages, 'Product added to cart successfully!'];
        setSuccessMessages(newSuccessMessages);
        setTimeout(() => {
          setSuccessMessages([]);
        }, 1250);
      })
      .catch((error) => {
        console.error('Error adding product to cart:', error);
      });

    const newMessage = 'Added';
    setSuccessMessages([newMessage]);
    setTimeout(() => {
      setSuccessMessages([]);
    }, 1150);
  };

  return (
    <div className="product-card" id={`sauces-product-${_id}`}>
      <img
        src={images[0].url}
        alt={title}
        className="animate__animated"
        onMouseEnter={(e) => e.target.classList.add('animate__pulse')}
        onMouseLeave={(e) => e.target.classList.remove('animate__pulse')}
      />
      <div className="content">
        <h2 style={{ fontSize: '19px' }}>{title}</h2>
        <p>Price: EGP {price}</p>
        {successMessages.map((message, index) => (
          <p key={index} className="success-message">{message}</p>
        ))}
        <button className="order-button" onClick={addToCart}>
          <CartButton />
        </button>
      </div>
    </div>
  );
};

export default SaucesCard;
