import React, { useState } from 'react';
import './index.css';
import axios from 'axios';
import CartButton from './CartButton';

const DrinksCard = ({ _id, title, images, ingredients, price }) => {
  const [successMessage, setSuccessMessage] = useState('');

  const addToCart = () => {
    const dataToSend = {
      productId: _id,
      productQuantity: 1,
      size: 150,
      extras: {},
    };

    axios
      .post('https://xfood-ob2l.onrender.com/add-to-cart', dataToSend, { withCredentials: true })
      .then((response) => {
        console.log('Product added to cart:', response.data);
        setSuccessMessage('Product added to cart successfully!');
        setTimeout(() => {
          setSuccessMessage('');
        }, 1250); // Clear success message after 1.25 seconds
      })
      .catch((error) => {
        console.error('Error adding product to cart:', error);
      });

    const newMessage = 'Added';
    setSuccessMessage(newMessage);
    setTimeout(() => {
      setSuccessMessage('');
    }, 1150); // Remove the "Added" message after 3 seconds
  };

  return (
    <div className="product-card" id={`drink-product-${_id}`}>
      <img
        src={images[0].url}
        alt={title}
        className="animate__animated"
        onMouseEnter={(e) => e.target.classList.add('animate__pulse')}
        onMouseLeave={(e) => e.target.classList.remove('animate__pulse')}
      />
      <div className="content">
        <h2 style={{ fontSize: '19px' }}>{title}</h2>
        <p>Price: EGP {price}</p>
        {successMessage && <p className="success-message">{successMessage}</p>}
        <button className="order-button" onClick={addToCart}>
          <CartButton />
        </button>
      </div>
    </div>
  );
};

export default DrinksCard;
