import React, { useState } from 'react';
import './index.css';
import axios from 'axios';
import CartButton from './CartButton';

const AppetizersCard = ({ _id, title, images, ingredients, price }) => {
  const [successMessages, setSuccessMessages] = useState([]);

  const addToCart = () => {
    const dataToSend = {
      productId: _id,
      productQuantity: 1,
      size: 150,
      extras: {},
    };

    axios
      .post('https://xfood-ob2l.onrender.com/add-to-cart', dataToSend, { withCredentials: true })
      .then((response) => {
        console.log('Product added to cart:', response.data);
        const newSuccessMessages = [...successMessages, 'Product added to cart successfully!'];
        setSuccessMessages(newSuccessMessages);
        setTimeout(() => {
          const filteredMessages = newSuccessMessages.filter((msg, index) => index !== 0);
          setSuccessMessages(filteredMessages);
        }, 1250); // Remove the oldest message after 1.25 seconds
      })
      .catch((error) => {
        console.error('Error adding product to cart:', error);
      });

    const newMessage = 'Added';
    setSuccessMessages(prevMessages => [...prevMessages, newMessage]);
    setTimeout(() => {
      setSuccessMessages(prevMessages => prevMessages.filter(msg => msg !== newMessage));
    }, 1150); // Remove the "Added" message after 3 seconds
  };

  return (
    <div className="product-card" id={`appetizers-product-${_id}`}>
      <img
        src={images[0].url}
        alt={title}
        className="animate__animated"
        onMouseEnter={(e) => e.target.classList.add('animate__pulse')}
        onMouseLeave={(e) => e.target.classList.remove('animate__pulse')}
      />
      <div className="content">
        <h2 style={{ fontSize: '19px' }}>{title}</h2>
        <p>Price: EGP {price}</p>
        {successMessages.map((message, index) => (
          <p key={index} className="success-message">{message}</p>
        ))}
        <button className="order-button" onClick={addToCart}>
          <CartButton />
        </button>
      </div>
    </div>
  );
};

export default AppetizersCard;
