import React, { useState, useContext } from 'react';
import { Link as RouterLink } from 'react-router-dom'; 
import './Login.css';
import axios from 'axios';
import { useHistory } from 'react-router-dom';
import { UserContext } from './UserContext'; 
import { ScaleLoader } from 'react-spinners'; 


const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [emailError, setEmailError] = useState('');
  const [passwordError, setPasswordError] = useState('');
  const history = useHistory();
  const { setUser } = useContext(UserContext); 
  const [loading, setLoading] = useState(false);

  

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
    setEmailError('');
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
    setPasswordError('');
  };

  const isValidEmail = (email) => {
    return /\S+@\S+\.\S+/.test(email);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
  
    if (!email || !password) {
      alert('Please fill in all fields.');
      return;
    }
  
    if (!isValidEmail(email)) {
      setEmailError('Please enter a valid email address');
      return;
    }
  
    try {
      const response = await axios.post('https://xfood-ob2l.onrender.com/login', {
        email,
        password,
      }, { withCredentials: true });
  
      console.log('Login successful:', response.data);
  
      const { username } = response.data;
  
      setUser({ isLoggedIn: true, username });
  
      setLoading(false);
  
      history.push('/');
      window.location.reload(); 
    } catch (error) {
      console.error('Login error:', error.response ? error.response.data : error.message);
      alert('Incorrect email or password. Please try again.');
    }
  };
  

  return (
    <div className="login-container">
      <div className="login-box">
        <h1>LOGIN</h1>
        <form onSubmit={handleSubmit}>
          
          {loading && (
            <div className="loading">
              <ScaleLoader
                color="#d73737"
                loading={loading}
                height={210}
                width={10}
                aria-label="Loading Spinner"
                data-testid="loader"
              />
            </div>
          )}

          <div className="form-group">
            <label htmlFor="email">E-MAIL:</label>
            <input
              className={`custom-input ${emailError ? 'error' : ''}`}
              type="email"
              id="email"
              name="email"
              placeholder="Enter your email"
              value={email}
              onChange={handleEmailChange}
            />
            {emailError && <span className="email-error">{emailError}</span>}
          </div>

          <div className="form-group">
            <label htmlFor="password">Password:</label>
            <input
              className={`custom-input ${passwordError ? 'error' : ''}`}
              type="password"
              id="password11"
              name="password"
              placeholder="Enter your password"
              value={password}
              onChange={handlePasswordChange}
            />
            {passwordError && <span className="password-error">{passwordError}</span>}
          </div>

          <button className="button_login" type="submit">
            Log In
          </button>

          <RouterLink to="/SignUp" className="sign-up-button">
            Create_an_account
          </RouterLink>
        </form>
      </div>
    </div>
  );
};

export default Login;
