import React, { useState, useEffect } from 'react';
import DrinksCard from './DrinksCard'; 

const Drinks = () => {
  const [drinks, setDrinks] = useState([]);

  useEffect(() => {
    fetch('https://xfood-ob2l.onrender.com/products?category=drinks')
      .then((response) => response.json())
      .then((data) => setDrinks(data))
      .catch((error) => console.error('Error fetching data:', error));
  }, []);

  return (
    <div id="drinks" className="sandwich-container">
      <div className="offer-container">
        <img src="titleMeals2/drinks.png" alt="Offer" className="drinks-section" />
      </div>
      <div className="sandwich-list">
        {drinks.map((drink, index) => (
          <DrinksCard key={index} {...drink} />
        ))}
      </div>
    </div>
  );
};

export default Drinks;
