import React from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import { LanguageProvider } from './components/LanguageContext';
import Home from './components/Home';
import Verification from './components/Verification';
import SignUp from './components/SignUp';
import Login from './components/Login';
import { UserProvider } from "./components/UserContext";
import ProductPage from './components/ProductPage';
import CartPage from './components/CartPage'; // Import the CartPage component
import CheckoutPage from './components/CheckoutPage';
import Dashboard from './components/Dashboard';
import PreviousOrder from './components/PreviousOrder';
function App() {
  return (
    <Router>
      <LanguageProvider>
        <UserProvider>
          <div className="App">
            <Switch>
              <Route exact path="/" component={Home} />
              <Route exact path="/verification" component={Verification} />
              <Route exact path="/SignUp" component={SignUp} /> 
              <Route exact path="/Login" component={Login} />
              <Route exact path="/product/:id" component={ProductPage} />
              <Route exact path="/cart" component={CartPage} />
              <Route exact path="/checkout" component={CheckoutPage} />
              <Route exact path="/dashboard" component={Dashboard} />
              <Route exact path="/previousorder" component={PreviousOrder} />
              <Route render={() => <Redirect to="/" />} />
            </Switch>
            <div className="scroll-to-top" onClick={() => window.scrollTo(0, 0)}>
              {/* Content for scroll-to-top */}
            </div>
          </div>
        </UserProvider>
      </LanguageProvider>
    </Router>
  );
}

export default App;
