import React, { useState, useEffect } from 'react';
import DessertsCard from './DessertsCard';

const Desserts = () => {
  const [desserts, setDesserts] = useState([]);

  useEffect(() => {
    fetch('https://xfood-ob2l.onrender.com/products?category=desserts')
      .then((response) => response.json())
      .then((data) => setDesserts(data))
      .catch((error) => console.error('Error fetching data:', error));
  }, []); 

  return (
    <div id="desserts" className="sandwich-container">
      <div className="offer-container">
        <img src="titleMeals2/desserts.png" alt="Offer" className="desserts-section" />
      </div>
      <div className="sandwich-list">
        {desserts.map((dessert, index) => (
          <DessertsCard key={index} {...dessert} />
        ))}
      </div>
    </div>
  );
};

export default Desserts;
