import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';
import './PreviousOrder.css'; // Import your CSS file here
import io from 'socket.io-client';
const PreviousOrder = () => {
  const [previousOrders, setPreviousOrders] = useState([]);
  const history = useHistory();

  useEffect(() => {
    axios.get('https://xfood-ob2l.onrender.com/previous-orders', { withCredentials: true })
      .then((response) => {
        const ordersFromAPI = response.data.orders || [];
        setPreviousOrders(ordersFromAPI);
      })
      .catch((error) => {
        console.error('Error fetching previous orders:', error);
      });
  }, []);

  const sendToCart = (order) => {
    order.items.forEach((item) => {
      const dataToSend = {
        productId: item._id,
        productQuantity: item.quantity,
        size: item.size,
        extras: item.ingredients.reduce((acc, ingredient) => {
          acc[ingredient] = true;
          return acc;
        }, {}),
      };

      axios.post('https://xfood-ob2l.onrender.com/add-to-cart', dataToSend, { withCredentials: true })
        .then((response) => {
          console.log('Product added to cart:', response.data);
        })
        .catch((error) => {
          console.error('Error adding product to cart:', error);
        });

      history.push('/cart', {
        productImage: item.productImage,
        productName: item.title,
        productPrice: item.default_price,
        quantity: item.quantity,
        weight: item.size,
        selectedCheckboxData: dataToSend.extras,
      });
    });
  };
  
  const socket = io("https://xfood-ob2l.onrender.com");
  socket.on('newOrder', (data) => {

    console.log(data);
  });
  return (
    <div className="container12345345">
      <h1 className="title12323">Previous Orders</h1>
      <div className="order-list123456">
        {previousOrders.map((order) => (
          <div className="orderhuhu" key={order._id}>
            {order.items.map((item, index) => (
              <div className="order-item122" key={index}>
                <img src={item.image[0].url} alt={item.title} className="product-image" />
                <p className="product-info23">Product: {item.title}</p>
                <p className="product-info23">Quantity: {item.quantity}</p>
                <p className="product-info23">Size: {item.size}</p>
                <p className="product-info23">Ingredients: {item.ingredients.join(', ')}</p>
                <button className="order-again-btn12" onClick={() => sendToCart(order)}>
                  Order Again
                </button>
              </div>
            ))}
          </div>
        ))}
      </div>
    </div>
  );
};

export default PreviousOrder;
