import React, { useState, useEffect } from 'react';
import './Dashboard.css';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTruck, faChevronRight, faBagShopping,faBurger,faShop,faUsers,faCircleExclamation } from "@fortawesome/free-solid-svg-icons";
import logoImage from '../images/logo/logo1.png';
import FormControl from '@mui/material/FormControl';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import axios from 'axios';
import { Toaster, toast } from 'sonner'


const Dashboard = () => {
  const [userData, setUserData] = useState(null);
  const [deliveryData, setDeliveryData] = useState([]);
  const [orderData, setOrderData] = useState([]);
  const [showDelivery, setShowDelivery] = useState(false);
  const [showOrders, setShowOrders] = useState(false);
  const [showProduct, setShowProduct] = useState(false);
  const [productData, setProductData] = useState([]);
  const [showEmployeeForm, setShowEmployeeForm] = useState(false);
  const [selectedIngredients, setSelectedIngredients] = useState([]);
  const [showSuccessMessage, setShowSuccessMessage] = useState(false);
  const [products, setProducts] = useState([]);
  const [showUpdateProducts, setShowUpdateProducts] = useState(false);
  const [selectedIngredient, setSelectedIngredient] = useState('');
  const [selectedAmount, setSelectedAmount] = useState('');
  const [newProduct, setNewProduct] = useState({
    title: '',
    description:'',                       
    ingredients: [],        
    images: [],            
    price_per_unit: '',     
    unit: '',               
    category: '',           
    price: '',              
  });

  const [names, setNames] = useState([]);

useEffect(() => {
  async function fetchIngredients() {
    try {
      const response = await fetch('https://xfood-ob2l.onrender.com/admin/ingredients', {
        method: 'GET',
        credentials: 'include' // Include credentials in the request
      });

      if (!response.ok) {
        throw new Error('Network response was not ok.');
      }
      
      const data = await response.json();
      
      // Assuming the API response contains an array of ingredient names
      if (Array.isArray(data)) {
        setNames(data);
      }
    } catch (error) {
      console.error('Error fetching ingredient names:', error);
      // Handle errors or set a default value for names if necessary
    }
  }

  fetchIngredients();
}, []);

useEffect(() => {
  // Fetch user data from the API
  async function fetchUserData() {
    try {
      const response = await fetch('https://xfood-ob2l.onrender.com/check-auth', {
        method: 'GET',
        credentials: 'include'
      });

      if (!response.ok) {
        throw new Error('Failed to fetch user data');
      }

      const userData = await response.json();
      setUserData(userData.user); // Set the user data in state
    } catch (error) {
      console.error('Error fetching user data:', error);
    }
  }

  fetchUserData();
}, []);


const isAuthorizedAdmin = () => {
  return userData && (userData.role === 'manager' || userData.role === 'delivery' ||userData.role === 'employee') && userData.admin === true;
};




const handleIncreaseIngredient = async () => {
  try {
    const response = await fetch('https://xfood-ob2l.onrender.com/admin/increase-ingredient', {
      method: 'PATCH',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        title: selectedIngredient,
        newQty: selectedAmount,
      })
    });

    if (!response.ok) {
      throw new Error('Network response was not ok.');
    }

    const result = await response.json();
    // Handle success, update UI or perform other actions
    console.log('Increased ingredient:', result);
    toast.success("Increased ingredient Successfully");
  } catch (error) {
    console.error('Error increasing ingredient:', error);
    toast.success("Increased ingredient Cancelled");
    // Handle errors or display an error message
  }
};


  const [editedProduct, setEditedProduct] = useState({
    id: '', // If needed
    title: '',
    description: '',
    ingredients: [],
    images: [],
    price_per_unit: '',
    unit: '',
    category: '',
    price: '',
    
  });
  

  useEffect(() => {
    // Fetch Products Data
    fetch('https://xfood-ob2l.onrender.com/products')
      .then((response) => response.json())
      .then((data) => {
        setProducts(data);
      })
      .catch((error) => {
        console.error('Error fetching product data:', error);
      });
  }, []);
  
  const [showEditForm, setShowEditForm] = useState(false);
  
  const handleEditProduct = async (productId) => {
  try {
    const response = await axios.get(`https://xfood-ob2l.onrender.com/products/single/${productId}`,{withCredentials:true});

    console.log('Response status:', response.status); 

    if (response.status !== 200) {
      throw new Error('Failed to fetch product data');
    }

    const productData = response.data;
    setEditedProduct(productData); 

    setShowEditForm(true); 
  } catch (error) {
    console.error('Error fetching product data:', error);
    
  }
};

  
const handleDeleteProduct = async (productId) => {
  const confirmation = window.confirm('Are you sure you want to delete this product?');
  
  if (confirmation) {
    try {
      const deleteResponse = await fetch(`https://xfood-ob2l.onrender.com/admin/delete-product/`, {
        method: 'DELETE',
        body: JSON.stringify({ id: productId }),
        credentials: "include",
        headers: {
          'Content-Type': 'application/json'
        }
      });
  
      
      if (!deleteResponse.ok) {
        throw new Error('Failed to delete product');
        
      }
      
      // Update
      setProducts(products.filter((product) => product._id !== productId));
      
      const deletedProduct = await deleteResponse.json();
      console.log('Product deleted:', deletedProduct);
      toast.success("Deleted Successfully");
    } catch (error) {
      console.error('Error deleting product:', error);
      toast.success("Failed to delete product");
    }
  } else {
    // Handle cancellation
    console.log('Delete operation cancelled');
    toast.success("Delete operation cancelled");
  }
};
  
  
  
const handleEditedProductChange = (e) => {
  const { name, value } = e.target;
  setEditedProduct((prevProduct) => ({
    ...prevProduct,
    [name]: value,
  }));
};

const handleSubmitProductUpdate = async (e, productId) => {
  e.preventDefault();

  // Log the current state of editedProduct after the state update
  console.log(editedProduct);

  
  const updatedData = new FormData();
  updatedData.append('id', editedProduct._id);
  updatedData.append('title', editedProduct.title);
  updatedData.append('description', editedProduct.description);
  updatedData.append('price', editedProduct.price);
  updatedData.append('category', editedProduct.category);
  updatedData.append('unit', editedProduct.unit);
  updatedData.append('price_per_unit', editedProduct.price_per_unit);
  updatedData.append('ingredients', editedProduct.ingredients);

  // Log updatedData after FormData is populated
  console.log(updatedData);

  // Send PATCH request to update product
  try {
    const updateResponse = await fetch(`https://xfood-ob2l.onrender.com/admin/update-product`, {
      method: 'PATCH',
      body: updatedData,
      credentials: 'include',
    });

    if (!updateResponse.ok) {
      throw new Error('Failed to update product');
    }

    const updatedProduct = await updateResponse.json();
    console.log('Product updated:', updatedProduct);
    // Handle success
  } catch (error) {
    console.error('Error updating product:', error);
  }
};


  const handleProductInputChange = (e) => {
    const { name, value } = e.target;
  
    if (name === 'ingredients') {
      const ingredientsArray = Array.isArray(value) ? value : value.split(',').map((ingredient) => ingredient.trim());
      setNewProduct({ ...newProduct, ingredients: ingredientsArray }); 
      setSelectedIngredients(ingredientsArray); 
    } else if (name === 'price_per_unit') {
      setNewProduct({ ...newProduct, price_per_unit: value });
    } else if (name === 'description') {
      setNewProduct({ ...newProduct, description: value });
    } else {
      setNewProduct({ ...newProduct, [name]: value });
    }
  };
  
  
  const [successMessage, setSuccessMessage] = useState('');
  const [showInventory, setShowInventory] = useState(false);
  const [ingredientData, setIngredientData] = useState([]);
  const [newIngredient, setNewIngredient] = useState({
    title: '',
    quantity: 0,
    price:0,
    unit:'',
    
  });

  const handleIngredientInputChange = (e) => {
    const { name, value } = e.target;
    setNewIngredient({ ...newIngredient, [name]: value });
  };

  
  useEffect(() => {
    const interval = setInterval(() => {
      
      fetch('https://xfood-ob2l.onrender.com/admin/orders', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
        credentials: 'include', 
      })
      .then((response) => response.json())
      .then((data) => {
        setOrderData(data);
  
        // Update order statuses here
        const statuses = {};
        data.forEach((order) => {
          statuses[order._id] = order.status;
        });
        setOrderStatuses(statuses);
      })
      .catch((error) => {
        console.error('Error fetching order data:', error);
      });
    }, 1000); // Adjust the interval timing as needed (e.g., 1000ms for 1 second)
  
    return () => clearInterval(interval); // Clear the interval on component unmount
  }, []);
  

  const [acceptedOrderId, setAcceptedOrderId] = useState('');

const handleAcceptOrder = (_id) => {
  fetch(`https://xfood-ob2l.onrender.com/admin/change-status`, {
    method: 'PATCH',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ orderStatus: 'preparing',orderId:_id }),
    credentials: 'include',
  })
    .then((response) => response.json())
    .then((data) => {
      console.log('Order accepted:', data);
      
      setAcceptedOrderId(_id);
    })
    .catch((error) => {
      console.error('Error accepting order:', error);
    });
};

const [ordersReadyStatus, setOrdersReadyStatus] = useState({});

const handleReadyOrder = (_id) => {
  fetch(`https://xfood-ob2l.onrender.com/admin/change-status`, {
    method: 'PATCH',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ orderStatus: 'out to delivery', orderId: _id }),
    credentials: 'include',
  })
    .then((response) => response.json())
    .then((data) => {
      console.log('Order marked as ready:', data);
      setShowDeliveredButton(true);
      setOrdersReadyStatus((prevStatus) => ({
        ...prevStatus,
        [_id]: true, // Mark this order as ready in the state
      }));
    })
    .catch((error) => {
      console.error('Error marking order as ready:', error);
    });
};


const [orderStatuses, setOrderStatuses] = useState({});

// Update the useEffect that fetches order data
useEffect(() => {
  fetch('https://xfood-ob2l.onrender.com/admin/orders', {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
    },
    credentials: 'include', 
  })
  .then((response) => response.json())
  .then((data) => {
    setOrderData(data);

    // Update order statuses here
    const statuses = {};
    data.forEach((order) => {
      statuses[order._id] = order.status;
    });
    setOrderStatuses(statuses);
  })
  .catch((error) => {
    console.error('Error fetching order data:', error);
  });
}, []);


const [showDeliveredButton, setShowDeliveredButton] = useState(false);

// Function to handle marking an order as delivered
const handleDeliveredOrder = (_id) => {
  fetch(`https://xfood-ob2l.onrender.com/admin/change-status`, {
    method: 'PATCH',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ orderStatus: 'delivered',orderId:_id }),
    credentials: 'include',
  })
    .then((response) => response.json())
    .then((data) => {
      console.log('Order marked as delivered:', data);
      
    })
    .catch((error) => {
      console.error('Error marking order as delivered:', error);
    });
};

  
  const handleRejectOrder = (_id) => {
    
    fetch(`https://xfood-ob2l.onrender.com/admin/change-status`, {
      method: 'PATCH',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ orderStatus: 'cancelled',orderId:_id }),
      credentials:"include",
    })
      .then((response) => response.json())
      .then((data) => {
        console.log('Order rejected:', data);
      })
      .catch((error) => {
        console.error('Error rejecting order:', error);
      });
  };
  
  
  
  const displayNotification = () => {
    if (!('Notification' in window)) {
      console.log('This browser does not support desktop notification');
    } else {
      Notification.requestPermission().then(function (permission) {
        if (permission === 'granted') {
          new Notification('New Waiting Order!', {
            body: 'A new order with status Waiting has arrived.',
          });

          const audio = new Audio('/audio/new_order.mp3');
          audio.play().catch((error) => {
            console.error('Error playing sound:', error);
          });
        }
      });
    }
  };

  useEffect(() => {
    fetch('https://xfood-ob2l.onrender.com/admin/orders', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'include', 
    })
    .then((response) => response.json())
    .then((data) => {
      setOrderData(data);
  
      // Update order statuses here
      const statuses = {};
      data.forEach((order) => {
        statuses[order._id] = order.status;
      });
      setOrderStatuses(statuses);
  
      // Check for new Waiting orders and trigger notification
      const hasNewWaitingOrder = data.some((order) => order.status === 'Waiting');
      if (hasNewWaitingOrder) {
        displayNotification(); // Call the notification function
      }
    })
    .catch((error) => {
      console.error('Error fetching order data:', error);
    });
  }, []);



    



    // Fetch Order Data
    fetch('https://xfood-ob2l.onrender.com/admin/orders', {
  method: 'GET',
  headers: {
    'Content-Type': 'application/json',
  },
  credentials: 'include', 
})
  .then((response) => response.json())
  .then((data) => {
    setOrderData(data);
  })
  .catch((error) => {
    console.error('Error fetching order data:', error);
  });



  const [showPopup, setShowPopup] = useState(false);
  const [selectedOrder, setSelectedOrder] = useState(null);

  // Function to handle showing order details in a popup
  const handleShowDetails = (order) => {
    setSelectedOrder(order);
    setShowPopup(true);
  };

  // Function to handle closing the popup
  const handleClosePopup = () => {
    setShowPopup(false);
  };

  const [filterStatus, setFilterStatus] = useState('');

  const handleStatusFilter = (selectedStatus) => {
    setFilterStatus(selectedStatus);
  };
  
  // Filter the orders based on the selected status
  const filteredOrderData = orderData.length>0 && orderData.filter((orderItem) => {
    if (!filterStatus) {
      return true; // Show all orders if no filter is applied
    }
    return orderItem.status === filterStatus;
  });
  
  useEffect(() => {
    const interval = setInterval(() => {
      // Fetch Delivery Data at intervals
      fetch('https://xfood-ob2l.onrender.com/admin/delivery-orders', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
        credentials: 'include',
      })
      .then((response) => response.json())
      .then((data) => {
        setDeliveryData(data);
      })
      .catch((error) => {
        console.error('Error fetching delivery data:', error);
      });
    }, 1000); // Adjust the interval timing as needed (e.g., 2000ms for 2 seconds)
  
    return () => clearInterval(interval); // Clear the interval on component unmount
  }, []);

  const handleDeliveryClick = () => {
    setShowDelivery(!showDelivery);
    setShowOrders(false);
    setShowProduct(false);
    setShowInventory(false);
    setShowEmployeeForm(false);
    setShowUpdateProducts(false);
  };

  const handleOrdersClick = () => {
    setShowOrders(!showOrders);
    setShowDelivery(false);
    setShowProduct(false);
    setShowInventory(false);
    setShowEmployeeForm(false);
    setShowUpdateProducts(false);
  };

  const handleInventoryClick = () => {
    setShowInventory(!showInventory);
    setShowDelivery(false);
    setShowOrders(false);
    setShowProduct(false);
    setShowEmployeeForm(false);
    setShowUpdateProducts(false);
  };

  const handleEmployeeButtonClick = () => {
    setShowEmployeeForm(!showEmployeeForm);
    setShowDelivery(false);
    setShowOrders(false);
    setShowProduct(false);
    setShowInventory(false);
    setShowUpdateProducts(false);
  };

  const handleProductClick = () => {
    setShowProduct(!showProduct);
    setShowDelivery(false);
    setShowOrders(false);
    setShowInventory(false);
    setShowEmployeeForm(false);
    setShowUpdateProducts(false);
  };

  const handleUpdateProductsClick = () => {
    setShowUpdateProducts(!showUpdateProducts);
    setShowDelivery(false);
    setShowOrders(false);
    setShowInventory(false);
    setShowEmployeeForm(false);
    setShowProduct(false);
  };
  

  

  const handleIngredientSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await fetch('https://xfood-ob2l.onrender.com/admin/add-ingredient', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        
        body: JSON.stringify(newIngredient),
        credentials: 'include', 
        
      });
      
      if (!response.ok) {
        throw new Error('Failed to add ingredient');
      }
  
      const data = await response.json();
      console.log('Ingredient added:', data);
      setNewIngredient({ title: '', quantity: 0, unit: '', price: 0 }); 
      setSuccessMessage('Ingredient added successfully!');
      setShowSuccessMessage(true);
      setTimeout(() => {
        setSuccessMessage('');
        setShowSuccessMessage(false);
      }, 1150);
    } catch (error) {
      console.error('Error adding ingredient:', error.message);
      toast.success("failed adding ingredient");
    }
  };


  const [newEmployee, setNewEmployee] = useState({
    First_name: '',
    Last_name: '',
    Email_address: '',
    Password: '',
    Role: '',
  });
  
  const handleEmployeeInputChange = (e) => {
    const { name, value } = e.target;
    setNewEmployee({ ...newEmployee, [name]: value });
  };
  
  const handleAddEmployee = (e) => {
    e.preventDefault();
    fetch('https://xfood-ob2l.onrender.com/admin/add-employee', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(newEmployee),
      credentials:"include",
    })
      .then((response) => response.json())
      .then((data) => {
        console.log('Employee added:', data);
        setNewEmployee({ First_name: '', Last_name: '', Email_address: '', Password: '', Role: '' });
        toast.success("added Successful");
      })
      .catch((error) => {
        console.error('Error adding employee:', error);
        toast.success("failed adding");
      });
  };
  
  

  const handleImageUpload = (e) => {
    const files = e.target.files;
          setNewProduct({ ...newProduct, images: files });

  };
  
  const handleProductSubmit = async (e) => {
    e.preventDefault();
  
    const multiPartData = new FormData();
    
    
    multiPartData.append('title', newProduct.title);
    multiPartData.append('description', newProduct.description);
    multiPartData.append('price', newProduct.price);
    multiPartData.append('category', newProduct.category);
    multiPartData.append('ingredients', JSON.stringify(newProduct.ingredients));
    multiPartData.append('unit', newProduct.unit);
    multiPartData.append('price_per_unit', newProduct.price_per_unit);
  console.log(multiPartData)
    
    for (let i = 0; i < newProduct.images.length; i++) {
      multiPartData.append('images', newProduct.images[i]);
      console.log(newProduct);
    }
  
    try {
      const response = await fetch('https://xfood-ob2l.onrender.com/admin/add-product', {
        method: 'POST',
        credentials: 'include',
        body: multiPartData,
        
      });
  toast.success("successful")
      if (!response.ok) {
        console.log(await response.json());
        throw new Error('Failed to add product');
      }
      
      const data = await response.json();
      console.log('Product added:', data);
      setProductData([...productData, data]);
      setNewProduct({
        title: '',
        description: '',
        ingredients: [],
        images: [],
        price_per_unit: '',
        unit: '',
        category: '',
        price: '',
      });
      setSuccessMessage('Product added successfully!');
      setShowSuccessMessage(true);
      setTimeout(() => {
        setSuccessMessage('');
        setShowSuccessMessage(false);
      }, 1150);
    } catch (error) {
      console.error('Error adding product:', error);
      toast.success("failed adding product");
    }
  };

 

  return (
    <div className='dashboardpage'>
      <img src={logoImage} alt="Logo" className="logo-imageeee animate__animated animate__rubberBand" />
      <div className="dashboard-container">
        <div className="button-containerr">
          <button className='deliverybutton' onClick={handleDeliveryClick}><FontAwesomeIcon className='deliverycar' icon={faTruck} style={{ color: "#ffffff", }} />Delivery <FontAwesomeIcon className='arrowdas' icon={faChevronRight} style={{ color: "#ffffff", }} /></button>
          <button onClick={handleOrdersClick}><FontAwesomeIcon className='bagg' icon={faBagShopping} style={{ color: "#ffffff", }} />Orders<FontAwesomeIcon className='arrowdas2' icon={faChevronRight} style={{ color: "#ffffff", }} /></button>
          <button onClick={handleProductClick}><FontAwesomeIcon className='bur' icon={faBurger} style={{color: "#ffffff",}} />Product<FontAwesomeIcon className='arrowdas3' icon={faChevronRight} style={{ color: "#ffffff", }} /></button>
          <button onClick={handleInventoryClick}><FontAwesomeIcon className='bur' icon={faShop} style={{color: "#ffffff",}} />Inventory<FontAwesomeIcon className='arrowdas4' icon={faChevronRight} style={{ color: "#ffffff", }} /></button>
          <button onClick={handleEmployeeButtonClick}><FontAwesomeIcon className='bur' icon={faUsers} style={{color: "#ffffff",}} />Employee<FontAwesomeIcon className='arrowdas4' icon={faChevronRight} style={{ color: "#ffffff", }} /></button>
          <button onClick={handleUpdateProductsClick}>{showUpdateProducts ? 'Hide Update Products' : 'Update Products'}<FontAwesomeIcon className='arrowdas5' icon={faChevronRight} style={{ color: "#ffffff", }} /></button>
        </div>
        
        {userData && isAuthorizedAdmin() && (
        <>
        
        {showDelivery && (
          <div className="delivery-section">
            <h2>Delivery</h2>
            <table>
              <thead>
                <tr>
                  <th>Order ID</th>
                  <th>Customer Name</th>
                  <th>total_price</th>
                  <th>payment_type</th>
                  <th>Address</th>
                  <th>Status</th>
                  <th>Date</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {deliveryData.map((deliveryItem) => (
                  <tr key={deliveryItem._id}>
                    <td>{deliveryItem._id}</td>
                    <td>{deliveryItem.customer_name}</td>
                    <td>{deliveryItem.total_price}</td>
                    <td>{deliveryItem.payment_type}</td>
                    <td>{deliveryItem.address}</td>
                    <td>{deliveryItem.status}</td>
                    <td>{deliveryItem.date}</td>
                    <td>{deliveryItem.status === 'out to delivery' && (
  <>
    {orderStatuses[deliveryItem._id] === 'delivered' ? (
      <span className="completed-text12">Completed</span>
    ) : (
      <button
        className="order-button12 delivered-button"
        onClick={() => handleDeliveredOrder(deliveryItem._id)}
      >
        Delivered
      </button>
    )}
  </>
)}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}

        {showOrders && (
          <div className="order-section">
            <h2>Orders</h2>
            <table>
            <select class="status-filter" onChange={(e) => handleStatusFilter(e.target.value)}>
  <option value="">Filter by Status</option>
  <option value="Waiting">Waiting</option>
  <option value="preparing">Preparing</option>
  <option value="out to delivery">Out to Delivery</option>
  <option value="delivered">Delivered</option>
  <option value="cancelled">Cancelled</option>
</select>

              <thead>
                <tr>
                  <th>Order ID</th>
                  <th>Customer Name</th>
                  <th>Status</th>
                  <th>Date</th>
                  <th>Action</th>
                  <th>Details</th>
                </tr>
              </thead>
              <tbody>
              {orderData.length>0 && filteredOrderData.map((orderItem) => (
  <tr key={orderItem._id}>
    <td>{orderItem._id}</td>
    <td>{orderItem.customer_name}</td>
    <td>{orderItem.status}</td>
    <td>{orderItem.date}</td>
    <td>
      {orderItem.status === 'Waiting' && (
        <>
          <button className="order-button accept-button" onClick={() => handleAcceptOrder(orderItem._id)}>Accept</button>
          <button className="order-button reject-button" onClick={() => handleRejectOrder(orderItem._id)}>Reject</button>
        </>
      )}

      {orderItem.status === 'preparing' && !ordersReadyStatus[orderItem._id] && (
        <button className="order-button ready-button" onClick={() => handleReadyOrder(orderItem._id)}>Ready</button>
      )}

      {orderItem.status === 'out to delivery' && (
        <>
          {orderStatuses[orderItem._id] === 'delivered' ? (
            <span className="completed-text">Completed</span>
          ) : (
            <button
              className="order-button delivered-button"
              onClick={() => handleDeliveredOrder(orderItem._id)}
            >
              Delivered
            </button>
          )}
        </>
      )}

      {orderItem.status === 'delivered' && (
        <span className="completed-text">Completed</span>
      )}

      {orderItem.status === 'cancelled' && (
        <span className="completed-text">UN Completed</span>
      )}
    </td>
    <td className="order-details-cell">
  <button className="order-details-button" onClick={() => handleShowDetails(orderItem)}>
  <FontAwesomeIcon icon={faCircleExclamation} style={{color: "#ff4f00",}} />
  </button>
</td>

  </tr>
))}

              </tbody>
            </table>
            {showPopup && selectedOrder && (
  <div className="popup">
    <div className="popup-content">
      <span className="close-button" onClick={handleClosePopup}>X</span>
      <h5>Order Details</h5>
      <p>Customer Name: {selectedOrder.customer_name}</p>
      <p>Date: {selectedOrder.date}</p>
      <p>Total Price: {selectedOrder.total_price}</p>
      <p>Address: {selectedOrder.address}</p>
      <p>Payment Type: {selectedOrder.payment_type}</p>

      <h6>Items:</h6>
      {selectedOrder.items.map((item, index) => (
        <div key={index}>
          <p>Product: {item.title}</p>
          <p>Quantity: {item.quantity}</p>
          <p>Size: {item.size}</p>
          <p>Default Price: {item.default_price}</p>
          <p>Total Price: {item.product_total_price}</p>

          <h6>Ingredients:</h6>
          <ul>
            {item.ingredients.map((ingredient, i) => (
              <li key={i}>{ingredient}</li>
            ))}
          </ul>
        </div>
      ))}
    </div>
  </div>
)}




          </div>
        )}


{showProduct && (
  
  <div className="product-form">

{showSuccessMessage && <p className="success-message1">{successMessage}</p>}

    <h2>Add New Product</h2>
    <form onSubmit={handleProductSubmit}>
      <input
        type="text"
        name="title"
        value={newProduct.title}
        onChange={handleProductInputChange}
        placeholder="Product Name"
      />
      <input
        type="text"
        name="price"
        value={newProduct.price}
        onChange={handleProductInputChange}
        placeholder="Price"
      />
      <input
        type="text"
        name="description"
        value={newProduct.description || ''}
        onChange={handleProductInputChange}
        placeholder="Description"
      />
      <select class="custom-select1"
  name="category"
  value={newProduct.category}
  onChange={handleProductInputChange}
  placeholder="Category"
>
  <option value="">Select a category</option>
  <option value="appetizers">Appetizers</option>
  <option value="sauces">Sauces</option>
  <option value="kids-meals">Kids Meals</option>
  <option value="desserts">Desserts</option>
  <option value="burger">Burger</option>
  <option value="chicken">Chicken</option>
  <option value="drinks">Drinks</option>
</select>

      <FormControl sx={{ m: 1, width: 380 }} className="custom-form-control">
      <InputLabel id="ingredients-label">Ingredients</InputLabel>
      <Select
        labelId="ingredients-label"
        id="ingredients"
        multiple
        value={selectedIngredients}
        onChange={(e) =>
          handleProductInputChange({ target: { name: 'ingredients', value: e.target.value } })
        }
        label="Ingredients"
        renderValue={(selected) => selected.join(', ')}
        className="custom-select"
      >
        {names.map((name) => (
          <MenuItem key={name} value={name}>
            {name}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
    <select class="custom-select1"
  name="unit"
  value={newProduct.unit}
  onChange={handleProductInputChange}
  placeholder="Unit"
>
  <option value="" disabled selected hidden>Select a unit</option>
  <option value="gram">gram</option>
  <option value="slice">slice</option>
</select>


<input class="custom-input123"
       type="number"
       name="price_per_unit"
       value={newProduct.price_per_unit}
       onChange={handleProductInputChange}
       placeholder="Price Per Unit"
/>

<input class="custom-file-input123"
       type="file"
       accept="image/*"
       onChange={(e) => handleImageUpload(e)}
/>

      <button type="submit">Add Product</button>
    </form>
  </div>
)}


{showInventory && (
        <div className="inventory-section">
          {showSuccessMessage && <p className="success-message1">{successMessage}</p>}
          <h2>Inventory Management</h2>
          <form onSubmit={handleIngredientSubmit}>
            <input
              type="text"
              name="title"
              value={newIngredient.title}
              onChange={handleIngredientInputChange}
              placeholder="Ingredient Name"
            />
            <input
              type="number"
              name="quantity"
              value={newIngredient.quantity}
              onChange={handleIngredientInputChange}
              placeholder="Quantity"
            />
            <div className="container12345">
              <select
                name="unit"
                value={newIngredient.unit}
                onChange={handleIngredientInputChange}
                className="select-input12345"
              >
                <option value="" disabled selected hidden>Unit</option>
                <option value="gm">gm</option>
                <option value="kg">kg</option>
              </select>
            </div>
            <input
              type="number"
              name="price"
              value={newIngredient.price}
              onChange={handleIngredientInputChange}
              placeholder="Price"
            />
            <button type="submit">Add Ingredient</button>
          </form>
          <ul>
            {ingredientData.map((ingredient) => (
              <li key={ingredient.id}>
                {ingredient.title}: {ingredient.quantity} {ingredient.unit} - ${ingredient.price}
              </li>
            ))}
          </ul>
          
          <button className='amountbt' onClick={() => setShowPopup(true)}>+ Amount</button>
          {showPopup && (
            <div className="popup">
              <div className="popup-content">
                <span className="close-button" onClick={() => setShowPopup(false)}>X</span>
                <h2>Select Ingredient and Amount</h2>
                <select onChange={(e) => setSelectedIngredient(e.target.value)}>
                  <option value="" disabled selected hidden>Select Ingredient</option>
                  {names.map((name, index) => (
                    <option key={index} value={name}>
                      {name}
                    </option>
                  ))}
                </select>
                <input
                  type="number"
                  value={selectedAmount}
                  onChange={(e) => setSelectedAmount(e.target.value)}
                  placeholder="Enter Amount"
                />
                <button onClick={handleIncreaseIngredient}>Increase Amount</button>
              </div>
              
            </div>
          )}
        </div>
      )}

{showEmployeeForm && (
  <div className="employee-form">
    <h2>Add New Employee</h2>
    <form onSubmit={handleAddEmployee}>
      <input
        type="text"
        name="First_name"
        value={newEmployee.First_name}
        onChange={handleEmployeeInputChange}
        placeholder="First Name"
      />
      <input
        type="text"
        name="Last_name"
        value={newEmployee.Last_name}
        onChange={handleEmployeeInputChange}
        placeholder="Last Name"
      />
      <select class="custom-select55"
        name="Role"
        value={newEmployee.Role}
        onChange={handleEmployeeInputChange}>
    <option value="" disabled selected hidden>Select Role</option>
    <option value="employee">Employee</option>
    <option value="delivery">Delivery</option>
</select>


      <input
        type="email"
        name="Email_address"
        value={newEmployee.Email_address}
        onChange={handleEmployeeInputChange}
        placeholder="Email Address"
      />
      <input
        type="password"
        name="Password"
        value={newEmployee.Password}
        onChange={handleEmployeeInputChange}
        placeholder="Password"
      />
      <button type="submit">Add Employee</button>
      
    </form>
    </div>
)}

{showUpdateProducts && (
  <div className="product-list-section">
    <h2>Update Products</h2>
    <div className="product-list">
      {products.map((product) => (
        <div key={product._id} className="product-item">
          {/* Display product details */}
          <h3>{product.title}</h3>
          <p>Description: {product.description}</p>
          <p>Ingredients: {product.ingredients.join(', ')}</p>
          <p>Price Per Unit: {product.price_per_unit}</p>
          <p>Unit: {product.unit}</p>
          <p>Category: {product.category}</p>
          <p>Price: {product.price}</p>
          {/* Check if 'images' is defined and not empty */}
          {product.images && product.images.length > 0 && (
            <div>
              Images:
              {product.images.map((image, index) => (
                <img key={index} src={image.url} alt={`Product ${index}`} />
              ))}
            </div>
          )}
          {/* Edit form */}
          {showEditForm && editedProduct._id === product._id ? (
  <div>
    <input
      type="text"
      name="title"
      value={editedProduct.title || ''}
      onChange={handleEditedProductChange}
      placeholder="Title"
    />
    <input
      type="text"
      name="description"
      value={editedProduct.description || ''}
      onChange={handleEditedProductChange}
      placeholder="Description"
    />
    <input
      type="text"
      name="ingredients"
      value={editedProduct.ingredients ? editedProduct.ingredients.join(', ') : ''}
      onChange={handleEditedProductChange}
      placeholder="Ingredients (comma-separated)"
    />
    <input
      type="text"
      name="price_per_unit"
      value={editedProduct.price_per_unit || ''}
      onChange={handleEditedProductChange}
      placeholder="Price Per Unit"
    />
    <input
      type="text"
      name="unit"
      value={editedProduct.unit || ''}
      onChange={handleEditedProductChange}
      placeholder="Unit"
    />
    <input
      type="text"
      name="category"
      value={editedProduct.category || ''}
      onChange={handleEditedProductChange}
      placeholder="Category"
    />
    <input
      type="text"
      name="price"
      value={editedProduct.price || ''}
      onChange={handleEditedProductChange}
      placeholder="Price"
    />
    <button onClick={(e) => handleSubmitProductUpdate(e, product._id)}>Save Changes</button>

  </div>
) : (
  <button className='editspace' onClick={() => handleEditProduct(product._id)}>Edit</button>
)}

          <button onClick={() => handleDeleteProduct(product._id)}>Delete</button>
        </div>
      ))}
    </div>
  </div>
)}

</>
      )}

<Toaster position="top-center" />
      </div>
    </div>
  );
};

export default Dashboard;
