import React, { useState, useEffect } from 'react';
import SaucesCard from './SaucCard';

const Saus = () => {
  const [sauces, setSauces] = useState([]);

  useEffect(() => {
    fetch('https://xfood-ob2l.onrender.com/products?category=sauces')
      .then((response) => response.json())
      .then((data) => setSauces(data))
      .catch((error) => console.error('Error fetching data:', error));
  }, []); 

  return (
    <div id="sauces" className="sandwich-container">
      <div className="offer-container">
        <img src="titleMeals2/sauces.png" alt="Offer" className="sus-section" />
      </div>
      <div className="sandwich-list">
        {sauces.map((sauce, index) => (
          <SaucesCard key={index} {...sauce} />
        ))}
      </div>
    </div>
  );
};

export default Saus;
