import React, { useState, useEffect } from 'react';
import AppetizersCard from './AppetizersCard';

const Appetizers = () => {
  const [appetizers, setAppetizers] = useState([]);

  useEffect(() => {
    fetch('https://xfood-ob2l.onrender.com/products?category=appetizers')
      .then((response) => response.json())
      .then((data) => setAppetizers(data))
      .catch((error) => console.error('Error fetching data:', error));
  }, []); 

  return (
    <div id="appetizers" className="sandwich-container">
      <div className="offer-container">
        <img src="titleMeals2/appetizers.png" alt="Offer" className="apt-section" />
      </div>
      <div className="sandwich-list">
        {appetizers.map((appetizer, index) => (
          <AppetizersCard key={index} {...appetizer} />
        ))}
      </div>
    </div>
  );
};

export default Appetizers;
