import React, { useState, useEffect } from 'react';
import { useLocation, useHistory } from 'react-router-dom';
import './CheckoutPage.css';
import logoImage from '../images/logo/logo1.png';
import ScaleLoader from 'react-spinners/ScaleLoader';

const CheckoutPage = () => {
  const [total_price, setTotalPrice] = useState(0);
  const [paymentMethod, setPaymentMethod] = useState('paypal');
  const [loading, setLoading] = useState(true);
  const [orderComplete, setOrderComplete] = useState(false);
  const history = useHistory();
  const location = useLocation();

  useEffect(() => {
    if (location.state && location.state.total) {
      setTotalPrice(location.state.total);
      setLoading(false);
    }
  }, [location.state]);

  const handlePaymentMethodChange = async (method) => {
    setPaymentMethod(method);
    if (method === 'cash') {
      try {
        const paymentData = {
          amount: total_price,
          paymentMethod: 'cash',
        };

        const response = await fetch('https://xfood-ob2l.onrender.com/make-order', {
          method: 'POST',
          body: JSON.stringify(paymentData),
          headers: {
            'Content-Type': 'application/json',
          },
          credentials: 'include',
        });

        const data = await response.json();

        if (response.ok) {
          console.log('Order placed successfully:', data);
          setOrderComplete(true);
          
          history.push('/home');
        } else {
          console.error('Failed to place order:', data);
        }
      } catch (error) {
        console.error('Error during checkout:', error);
      }
    } else if (method === 'paypal') {
      try {
        window.location.href = 'https://xfood-ob2l.onrender.com/payment';
      } catch (error) {
        console.error('Error during PayPal checkout:', error);
      }
    }
  };

  return (
    <div className="background-wrapper">
      {loading ? (
        <div className="loading-spinner">
          <ScaleLoader
            color="#d73737"
            loading={loading}
            height={180}
            width={7}
            aria-label="Loading Spinner"
            data-testid="checkout-loader"
          />
        </div>
      ) : (
        <div>
          <img src={logoImage} alt="Logo" className="logo-imageee animate__animated animate__rubberBand" />
          <div className="checkout-container">
            <h2>Checkout</h2>
            <div className="order-summary">
              <h3>Order Summary</h3>
              <p>Total: EGP {total_price}</p>
            </div>
            <div className="payment-methods">
              <h3>Select Payment Method</h3>
              <button
                className={paymentMethod === 'paypal' ? 'active' : ''}
                onClick={() => handlePaymentMethodChange('paypal')}
              >
                PayPal
              </button>
              <button
                className={paymentMethod === 'cash' ? 'active' : ''}
                onClick={() => handlePaymentMethodChange('cash')}
              >
                Cash on Delivery
              </button>
            </div>
            {orderComplete && <p className="order-complete-message">Order Complete!</p>}
          </div>
        </div>
      )}
    </div>
  );
};

export default CheckoutPage;
