import React, { useState, useEffect } from "react";
import { useLanguage } from "./LanguageContext"; 
import "./index.css"; 
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCartShopping, faGlobe ,faChevronDown,faToggleOff ,faToggleOn} from "@fortawesome/free-solid-svg-icons";
import 'animate.css';
import logoImage from '../images/logo/logo1.png'; 
import imag1 from "../images/buttons_up/offers.webp";
import imag2 from "../images/buttons_up/burger.webp";
import imag3 from "../images/buttons_up/chiken.webp";
import imag4 from "../images/buttons_up/apatizers.webp";
import imag5 from "../images/buttons_up/sauces.webp";
import imag6 from "../images/buttons_up/kidsmeal.webp";
import imag7 from "../images/buttons_up/dessert.webp";
import imag8 from "../images/buttons_up/drinks.webp";
import { Link as ScrollLink, animateScroll as scroll } from "react-scroll";
import { Link as RouterLink } from "react-router-dom";
import axios from 'axios';
import NightsStayIcon from '@mui/icons-material/NightsStay';
import LightModeIcon from '@mui/icons-material/LightMode';


const buttonStyle = {
  width: "100px", 
  height: "100px", 
  backgroundSize: "cover", 
  cursor: "pointer", 
};

const Nav = () => {
  
  const [isSunVisible, setIsSunVisible] = useState(true);
  const [isMoonVisible, setIsMoonVisible] = useState(false);
  const [isDarkMode, setIsDarkMode] = useState(false);
  const [cart, setCart] = useState([]);
  const [showLogout, setShowLogout] = useState(false);


const handleLogout = async () => {
  try {
    const response = await axios.post(
      "https://xfood-ob2l.onrender.com/logout",{},
      { withCredentials: true }
    );
      console.log("Logout successful", response.data);
      window.location.href = "http://localhost:3000/";
    }
    catch (error) {
    console.error("Error logging out:", error);
    // Handle logout failure or errors here
  }
};
  
  
  
  
  const handleSunClick = () => {
  if (isSunVisible) {
    setIsSunVisible(false);
    setIsMoonVisible(true);
    document.body.style.backgroundColor = '#111'; 
    document.body.style.color = '#CACACA';
  }
};

  useEffect(() => {
    axios.get('https://xfood-ob2l.onrender.com/cart-items',{ withCredentials: true })
      .then((response) => {
        const cartItems = response.data.cart;
        setCart(cartItems);
      })
      .catch((error) => {
        console.error('Error fetching cart items:', error);
      });
  }, []);

const handleMoonClick = () => {
  if (isMoonVisible) {
    setIsSunVisible(true);
    setIsMoonVisible(false);
    document.body.style.backgroundColor = '#fff'; // Change body background to white
    document.body.style.color = '#111';
    
  }
};

  const { language, toggleLanguage, translations } = useLanguage();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [firstname, setUsername] = useState("");
  
  useEffect(() => {
  axios.get("https://xfood-ob2l.onrender.com/check-auth", { withCredentials: true })
    .then((response) => {
      const { isLoggedIn, firstname } = response.data.user;
      setIsLoggedIn(true);
      setUsername(firstname);
      console.log(response);
    })
    .catch((error) => {
      console.error("Error fetching user data:", error);
    });
}, []);

const calculateTotalQuantity = () => {
  return cart.reduce((acc, item) => acc + parseInt(item.qty), 0);
};

const [total_quantity, setTotalQuantity] = useState(0);

useEffect(() => {
  setTotalQuantity(calculateTotalQuantity());
}, [cart]);

const handleGoToPreviousOrders = () => {
  // Redirect to the previous orders page
  // Replace this line with the appropriate route or URL for the previous orders page
  window.location.href = "/PreviousOrder";
};
    
  return (
    <div className="nav-container" style={{ backgroundImage: "black", display: "flex", flexDirection: "column", alignItems: "center", padding: "10px" }}>
      <div className="nav-top" style={{ display: "flex", justifyContent: "space-between", alignItems: "center", width: "100%" }}>
        
        <div style={{ display: "flex", alignItems: "center" }}>
          <FontAwesomeIcon icon={faGlobe} className="navicon"/>
          <button className="language-button" onClick={toggleLanguage}>
            {language}
          </button>
          <div className="chart" style={{ marginRight: "1px" }}>
          <RouterLink
            to="/cart"
            onClick={() => {
            window.location.href = "/cart";
            
  }}
>
  <div className="circle">
    <FontAwesomeIcon icon={faCartShopping} style={{ color: "#ffffff" }} />
    <span className="item-count">{total_quantity}</span>
  </div>
</RouterLink>

            <div onClick={isSunVisible ? handleSunClick : handleMoonClick} style={{ position: 'relative' }}>
  <LightModeIcon
    className={`sun ${isSunVisible ? 'visible' : 'hidden'}`}
    style={{ color: '#e69d00', display: isSunVisible ? 'block' : 'none' }}
  />
  <NightsStayIcon
    className={`moon ${isMoonVisible ? 'visible' : 'hidden'}`}
    style={{ color: '#474747', display: isMoonVisible ? 'block' : 'none' }}
  />
</div>

          </div>
        </div>
        
        <div className="nav-middle">
        {isLoggedIn ? (
          <div  className="welcome-message" >
            WELCOME {firstname} 
          </div>
          
        ) : (
          <>
            <RouterLink to="/Login" id="navlog" className="nav-buttonn">
              {translations[language].Login}
            </RouterLink>
            <RouterLink to="/SignUp" className="nav-buttonn">
              {translations[language].Create_an_account}
            </RouterLink>
          </>
        )}
      </div>
      {isLoggedIn && (
        <div onClick={() => setShowLogout(!showLogout)}>
          <FontAwesomeIcon className="arrow" icon={faChevronDown} />
          {showLogout && (
            <div className={`logout-button-slide ${showLogout ? '' : 'slide-up'}`}>
              <button onClick={handleLogout}>Logout</button>
            </div>
          )}
          {showLogout && (
            <div className={`logout-button-slidee ${showLogout ? '' : 'slide-up'}`}>
              <button onClick={handleGoToPreviousOrders}>Previous Orders</button>
            </div>
          )}
        </div>
      )}
      </div>
      <img src={logoImage} alt="Logo" className="logo-image animate__animated animate__rubberBand" />
      <div className="nav-bottom">
        <ScrollLink to="offer" smooth={true} duration={100}>
          <div style={{ backgroundImage: `url(${imag1})`, ...buttonStyle }} className="nav-button">
            {translations[language].Button1}
          </div>
        </ScrollLink>
        <ScrollLink to="Burger" smooth={true} duration={100}>
          <div style={{ backgroundImage: `url(${imag2})`, ...buttonStyle }} className="nav-button">
            {translations[language].Button2}
          </div>
        </ScrollLink>
        <ScrollLink to="chicken" smooth={true} duration={100}>
          <div style={{ backgroundImage: `url(${imag3})`, ...buttonStyle }} className="nav-button">
            {translations[language].Button3}
          </div>
        </ScrollLink>
        <ScrollLink to="appetizers" smooth={true} duration={100}>
          <div style={{ backgroundImage: `url(${imag4})`, ...buttonStyle }} className="nav-button">
            {translations[language].Button4}
          </div>
        </ScrollLink>
        <ScrollLink to="sauces" smooth={true} duration={100}>
          <div style={{ backgroundImage: `url(${imag5})`, ...buttonStyle }} className="nav-button">
            {translations[language].Button5}
          </div>
        </ScrollLink>
        <ScrollLink to="kidsmeal" smooth={true} duration={100}>
          <div style={{ backgroundImage: `url(${imag6})`, ...buttonStyle }} className="nav-button">
            {translations[language].Button6}
          </div>
        </ScrollLink>
        <ScrollLink to="desserts" smooth={true} duration={100}>
          <div style={{ backgroundImage: `url(${imag7})`, ...buttonStyle }} className="nav-button">
            {translations[language].Button7}
          </div>
        </ScrollLink>
        <ScrollLink to="drinks" smooth={true} duration={100}>
          <div style={{ backgroundImage: `url(${imag8})`, ...buttonStyle }} className="nav-button">
            {translations[language].Button8}
          </div>
        </ScrollLink>
      </div>
      
    </div>
  );
}

export default Nav;
