import React, { useState, useEffect } from 'react';
import ChickenSANCard from './ChickenSANCard';

const ChickenSAN = () => {
  const [sandwiches, setSandwiches] = useState([]);

  useEffect(() => {
    
    fetch('https://xfood-ob2l.onrender.com/products?category=chicken')
      .then((response) => response.json())
      .then((data) => setSandwiches(data))
      .catch((error) => console.error('Error fetching data:', error));
  }, []);

  return (
    <div id="chicken" className="sandwich-container">
      <div className="offer-container">
        <img src="titleMeals2/chikenSAN.png" alt="Offer" className="burger-section" />
      </div>
      <div className="sandwich-list">
        {sandwiches.map((sandwich, index) => (
          <ChickenSANCard key={index} {...sandwich} />
        ))}
      </div>
    </div>
  );
};

export default ChickenSAN;
