import React, { useEffect, useState } from 'react';
import { Link as RouterLink, useLocation, useHistory } from 'react-router-dom';
import './ProductPage.css';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSquarePlus, faSquareMinus , faCartShopping , faChevronLeft,faGlobe} from "@fortawesome/free-solid-svg-icons";
import { useLanguage } from "./LanguageContext"; 
import axios from 'axios';
import ScaleLoader from 'react-spinners/ScaleLoader';

const ProductPage = () => {
  const location = useLocation();
  const history = useHistory();
  const [loading, setLoading] = useState(true);
  const [refreshedOnce, setRefreshedOnce] = useState(false);
  const [selectedIngredients, setSelectedIngredients] = useState({
    tomatos: true, 
    lettuce: true,
    onions: true,
    mozarella: true,
    meat: true,
    'extra-tomatos': false,
    'extra-lettuce': false,
    'extra-onions': false,
    'extra-mozarella': false,
    'extra-meat': false,
  });


  useEffect(() => {
    const fetchData = async () => {
      try {
        
        await new Promise(resolve => setTimeout(resolve, 990));
        setLoading(false);
      } catch (error) {
        console.error('Error fetching data:', error);
        setLoading(false);
      }
    };
  
    fetchData(); 
  }, []);
  
  useEffect(() => {
    const reloadPage = () => {
      const hasReloaded = sessionStorage.getItem('hasReloaded');
      if (!hasReloaded) {
        sessionStorage.setItem('hasReloaded', 'true');
        window.location.reload();
      }
    };
  
    if (loading && !refreshedOnce) {
      setRefreshedOnce(true);
      setTimeout(() => {
        reloadPage();
      }, 1000); 
    }
  }, [0]);
  
  
  const handleCheckboxChange = (event) => {
    const { name, checked } = event.target;
    setSelectedIngredients((prevSelected) => ({
      ...prevSelected,
      [name]: checked,
    }));


    if (name.includes('extra-')) {
      const extraPrice = checked ? extraIngredientsPrices[name] || 0 : 0;
      const newBasePrice = checked ? basePrice + extraPrice : basePrice - extraPrice;
      setBasePrice(newBasePrice);
    }

    if (name.includes('extra-') && !checked) {
      const extraPrice = extraIngredientsPrices[name] || 0;
      setBasePrice((prevBasePrice) => prevBasePrice - extraPrice);
    }


  };

  const extraIngredientsPrices = {
    'extra-tomatos': 7,
    'extra-lettuce': 5,
    'extra-onions': 6,
    'extra-mozarella': 8,
    'extra-meat': 50,
  };
  
  const { productId,productName, productImage, productIngredients, productPrice, unit} = location.state || {};
  console.log(unit);
  const [quantity, setQuantity] = useState(1);
  const [basePrice, setBasePrice] = useState(productPrice);
  const [weight, setweight] = useState(150);
  const [prevweight, setprevweight] = useState(150);
  const[increaseIF,setincreaseIF]=useState(0);
  useEffect(() => {
    const hasReloaded = sessionStorage.getItem('hasReloaded');
    if (!hasReloaded) {
      sessionStorage.setItem('hasReloaded', 'true');
    }
  }, []);

  const SizeSelection=(size)=>{
    setprevweight(weight)
    setweight(size)
  }

  useEffect(() => {
  
    const priceIncreaseFactor = weight === 150 ? 0  : (weight - 150) * Number(unit); 
    setincreaseIF(priceIncreaseFactor);
    const newprice=(productPrice+priceIncreaseFactor)*quantity;

    setBasePrice(newprice);
  
}, [weight]);
  const incrementQuantity = () => {
    
    const newprice=(productPrice+increaseIF)*(quantity+1);
      setBasePrice(newprice);
    setQuantity(prevQuantity => 
    {
      
      return(
        quantity+1
      )
      
    }); 
  
  };

  const decrementQuantity = () => {
    
    
    if (quantity > 1) {
          setQuantity(prevQuantity => 
            {
              const newprice=(productPrice+increaseIF)*(quantity-1);
      setBasePrice(newprice);
              
      return(
        quantity-1
      )
            });
    }
  };

  const sendToCart = () => {

    const selectedCheckboxData = Object.keys(selectedIngredients)
      .filter((key) => selectedIngredients[key])
      .reduce((acc, key) => {
        acc[key] = extraIngredientsPrices[key] || '';
        return acc;
      }, {});

      
  const dataToSend = {
    productId: productId,
    productQuantity: quantity,
    size: weight,
    extras: selectedCheckboxData,
    
  };
  
  axios.post('https://xfood-ob2l.onrender.com/add-to-cart', dataToSend,{ withCredentials: true })
    .then((response) => {
      console.log('Product added to cart:', response.data);
    })
    .catch((error) => {
      console.error('Error adding product to cart:', error);
    });

    console.log(JSON.stringify(selectedCheckboxData));
    console.log(selectedCheckboxData);

    history.push('/cart', {
      productImage:productImage,
      productName: productName,
      productPrice: basePrice,
      quantity: quantity,
      weight:weight,
      selectedCheckboxData: selectedCheckboxData,
    });
    
  };

  const { language, toggleLanguage, translations } = useLanguage();

  return (
    <div className="product-page-full">
      {loading ? (
        <div className="loading-spinner">

          <ScaleLoader
            color="#d73737"
            loading={loading}
            height={200}
            width={10}
            aria-label="Loading Spinner"
            data-testid="product-page-loader"
          />
        </div>
      ) : (
      <div className="product-page">
      <FontAwesomeIcon icon={faGlobe} className="worldcart"/>
        <button className="language123-button" onClick={toggleLanguage}>
            {language}
          </button>
        <img src="/logo2/logo111.png" alt="Company Logo" className="product1234-image" />
        <div className="product-image-container">
          <img src={productImage[0].url} alt={productName} className="product-image" />
        </div>
        <div className="product-info">
          <p>Ingredients: {productIngredients}</p>
          <p className='price-cart'>Price: EGP {basePrice}</p>
        </div>
        <div className="product-info-container">
          <h2 className='selest-size'>Select Size for {productName}</h2>
          <div className="quantity-buttons">
            <h3 className='qn'>Quantity:</h3>
            <div onClick={decrementQuantity}><FontAwesomeIcon className='min-button' icon={faSquareMinus} style={{color: "#ff4f00"}} /></div>
            <span className='number-burger'>{quantity}</span>
            <div onClick={incrementQuantity}><FontAwesomeIcon className='plus-button' icon={faSquarePlus} style={{color: "#ff4f00"}} /></div>
          </div>
          <div className="size-options">
          <label>
  <input type="radio" name="size" onClick={() => SizeSelection(150)} defaultChecked />
  <span class="icon"></span>
  <span class="text">150 gm</span>
</label>

  <label>
    <input type="radio" name="size" onClick={() => SizeSelection(200)} />
    <span class="icon"></span>
    <span class="text">200 gm</span>
    

  </label>
  <label>
    <input type="radio" name="size" onClick={() => SizeSelection(300)} />
    <span class="icon"></span>
    <span class="text">300 gm</span>
    
  </label>
</div>
     <h3 className='extraname'>Extra Ingredients:</h3>     
     <div className="ingredients-box">
  <div className="extra-ingredients">
  <div className="extra-ingredients top-row">
  <div className="checkbox-box">
    <label>
      <input
        type="checkbox"
        name="tomatos"
        checked={selectedIngredients['tomatos']}
        onChange={handleCheckboxChange}
      />
      Tomatos 
    </label>
  </div>
  <br />
  <div className="checkbox-box">
    <label>
      <input
        type="checkbox"
        name="lettuce"
        checked={selectedIngredients['lettuce']}
        onChange={handleCheckboxChange}
      />
      Lettuce
    </label>
  </div>
  <br />
  <div className="checkbox-box">
    <label>
      <input
        type="checkbox"
        name="onions"
        checked={selectedIngredients['onions']}
        onChange={handleCheckboxChange}
      />
      Onions
    </label>
  </div>
  <br />
  <div className="checkbox-box">
    <label>
      <input
        type="checkbox"
        name="mozarella"
        checked={selectedIngredients['mozarella']}
        onChange={handleCheckboxChange}
      />
      Mozzarella Sticks
    </label>
  </div> 
  <br />
  <div className="checkbox-box">
    <label>
      <input
        type="checkbox"
        name="meat"
        checked={selectedIngredients['meat']}
        onChange={handleCheckboxChange}
      />
      One Burger
    </label>
  </div>
  <br />
</div>

<div className="extra-ingredients bottom-row">
  <div class="checkbox-box">
    <label>
      <input
        type="checkbox"
        name="extra-tomatos"
        checked={selectedIngredients['extra-tomatos']}
        onChange={handleCheckboxChange}
      />
      Extra Tomatos (+7)
    </label>
  </div>
  <br />
  <div class="checkbox-box">
    <label>
      <input
        type="checkbox"
        name="extra-lettuce"
        checked={selectedIngredients['extra-lettuce']}
        onChange={handleCheckboxChange}
      />
      Extra Lettuce (+5)
    </label>
  </div>
  <br />
  <div class="checkbox-box">
    <label>
      <input
        type="checkbox"
        name="extra-onions"
        checked={selectedIngredients['extra-onions']}
        onChange={handleCheckboxChange}
      />
      Extra Onions (+6)
    </label>
  </div>
  <br />
  <div class="checkbox-box">
    <label>
      <input
        type="checkbox"
        name="extra-mozarella"
        checked={selectedIngredients['extra-mozarella']}
        onChange={handleCheckboxChange}
      />
      Mozzarella Sticks (+8)
    </label>
  </div> 
  <br />
  <div class="checkbox-box">
    <label>
      <input
        type="checkbox"
        name="extra-meat"
        checked={selectedIngredients['extra-meat']}
        onChange={handleCheckboxChange}
      />
      extra Burger (+50)
    </label>
  </div> 
</div>
<div>
    </div>
  </div>
</div>
        
          
        </div>
      </div>
      )}
      <footer className={`footer123 ${loading ? 'hidden' : ''}`}>
        <div className="buttons-container">
          <RouterLink to="/order" className="CustomRouterLink">
            <FontAwesomeIcon icon={faChevronLeft} className='arrow123' style={{color: "#b9b8b8"}} />
            Back to Order
          </RouterLink>
          <button className="sendcart" onClick={sendToCart}>
            Add to Cart<FontAwesomeIcon icon={faCartShopping} className='cartbutton123' style={{color: "#b9b8b8"}} />
          </button>
        </div>
      </footer>
      
    </div>
  );
};

export default ProductPage;
