import React, { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import './index.css';
import CartButton from './CartButton';

const ChickenSANCard = ({ _id, title, images, ingredients, price, price_per_unit }) => {
  const [refreshedOnce, setRefreshedOnce] = useState(false);

  const handleRefresh = () => {
    if (!refreshedOnce) {
      setRefreshedOnce(true);
      setTimeout(() => {
        window.location.reload(); 
      }, 100); 
    }
  };

  return (
    <div className="product-card" id={`chicken-product-${_id}`}>
      <RouterLink
        to={{
          pathname: `/product/${_id}`,
          state: {
            productId: _id,
            productName: title,
            productImage: images,
            productIngredients: ingredients,
            productPrice: price,
            unit: price_per_unit,
          },
        }}
        className="link"
        onClick={handleRefresh}
      >
        <img
          src={images[0].url}
          alt={title}
          className="animate__animated"
          onMouseEnter={(e) => e.target.classList.add('animate__pulse')}
          onMouseLeave={(e) => e.target.classList.remove('animate__pulse')}
        />
      </RouterLink>
      <div className="content">
        <h2 style={{ fontSize: "19px" }}>{title}</h2>
        <p>Ingredients: {ingredients.join(', ')}</p>
        <p>Price: EGP {price}</p>
        <RouterLink
          to={{
            pathname: `/product/${_id}`,
            state: {
              productId: _id,
              productName: title,
              productImage: images,
              productIngredients: ingredients,
              productPrice: price,
              unit: price_per_unit,
            },
          }}
          className="order-link"
          onClick={handleRefresh} 
        >
          <button className='order-button'>
            <CartButton />
          </button>
        </RouterLink>
      </div>
    </div>
  );
};

export default ChickenSANCard;
