import React, { useState } from 'react';
import './SignUp.css';
import axios from 'axios';
import { Link as RouterLink, useHistory } from "react-router-dom"; 


const SignUp = () => {
  const [showPassword, setShowPassword] = useState(false);
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [passwordError, setPasswordError] = useState('');
  const [emailError, setEmailError] = useState('');
  const [address, setAddress] = useState('');
  const [submittedData, setSubmittedData] = useState(null); 
  const history = useHistory();


  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
    setPasswordError('');
  };

  const handleConfirmPasswordChange = (e) => {
    setConfirmPassword(e.target.value);
    setPasswordError('');
  };

  const handleEmailChange = (e) => {
    const email = e.target.value;
    setEmailError(email && !isValidEmail(email) ? 'Please enter a valid email address 📩' : '');
  };

  const isValidEmail = (email) => {
    return /\S+@\S+\.\S+/.test(email);
  };

  const handlePhoneNumberChange = (e) => {
    e.target.value = e.target.value.replace(/\D/g, '');
  };

  const handleAddressChange = (e) => {
    setAddress(e.target.value);
  };

  const getPasswordStrength = (password) => {
    const hasLetter = /[a-zA-Z]/.test(password);
    const hasNumber = /\d/.test(password);
    const hasSymbol = /[!@#$%^&*(),.?":{}|<>]/.test(password);

    if (password.length < 8 || !hasLetter || !hasNumber || !hasSymbol) {
      return 'Weak';
    } else if (password.length < 10) {
      return 'Medium';
    } else {
      return 'Strong';
    }
  };
  

  const handleSubmit = async (e) => {
    e.preventDefault();
  
    const firstname = e.target.firstname.value.trim();
    const lastname = e.target.lastname.value.trim();
    const age = e.target.age.value.trim();
    const email = e.target.email.value.trim();
    const phoneNumber = e.target.phoneNumber.value.trim();
    const enteredAddress = e.target.address.value.trim();
  
    setAddress(enteredAddress);
  
    if (!firstname || !lastname || !age || !email || !password || !confirmPassword || !address || !phoneNumber) {
      alert('Please fill in all fields.');
      return;
    }
  
    if (password !== confirmPassword) {
      setPasswordError('Passwords do not match. Please enter the correct password.');
      return;
    }
  
    if (!isValidEmail(email)) {
      setEmailError('Please enter a valid email address');
      return;
    }
  
    if (getPasswordStrength(password) === 'Weak') {
      setPasswordError('Password is very weak');
      return;
    }
  
    if (parseInt(age, 10) > 122) {
      alert('Are you still alive?');
      return;
    }
  
    try {
      const response = await axios.post('https://xfood-ob2l.onrender.com/register', {
        firstname,
        lastname,
        age,
        email,
        password,
        address,
        phoneNumber,
      } ,{withCredentials:true});
        
        setSubmittedData(response.data);
        history.push('/Verification');
      
    } catch (error) {
      console.log(error)
      
        alert('Account already exists.');
      
    }
  };
  

  return (
    <div className="signup-container">
      <div className="signup-box">
        <h1>SIGN-UP</h1>
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="firstName">First Name:</label>
            <input className="custom-input" type="text" id="firstName" name="firstname" placeholder="Enter your firstName" />
          </div>

          <div className="form-group">
            <label htmlFor="lastName">Last Name:</label>
            <input className="custom-input" type="text" id="lastName" name="lastname" placeholder="Enter your lastName" />
          </div>

          <div className="form-group">
            <label htmlFor="age">Age:</label>
            <input className="custom-input" type="number" id="age" name="age" min="13" placeholder="Enter your age" />
          </div>

          <div className="form-group">
            <label htmlFor="email">E-MAIL:</label>
            <input
              className={`custom-input ${emailError ? 'error' : ''}`}
              type="email"
              id="email"
              name="email"
              placeholder="Enter your email"
              onChange={handleEmailChange}
            />
            <div>{emailError && <span className="email-error">{emailError}</span>}</div>
          </div>

          <div className="form-group">
            <label htmlFor="password">Password:</label>
            <div className="password-input-container">
              <input
                className="custom-input1"
                type={showPassword ? 'text' : 'password'}
                id="password"
                name="password"
                placeholder="**********"
                onChange={handlePasswordChange}
              />
              <i
                className={`password-toggle-icon ${showPassword ? 'visible' : 'hidden'}`}
                onClick={togglePasswordVisibility}
              >
                {showPassword ? '👁️' : '👁️‍🗨️'}
              </i>
              {password && (
                <span className={`password-strength ${getPasswordStrength(password).toLowerCase()}`}>
                  {getPasswordStrength(password)}
                </span>
              )}
            </div>
          </div>

          <div className="form-group">
            <label htmlFor="confirmPassword">Confirm Password:</label>
            <div className="password-input-container">
              <input
                className="custom-input1"
                type={showPassword ? 'text' : 'password'}
                id="confirmPassword"
                name="confirmPassword"
                placeholder="**********"
                onChange={handleConfirmPasswordChange}
              />
              {confirmPassword && (
                <span className={`password-strength ${getPasswordStrength(confirmPassword).toLowerCase()}`}>
                  {getPasswordStrength(confirmPassword)}
                </span>
              )}
            </div>
          </div>

          <div className="form-group">
            <label htmlFor="phoneNumber">Phone Number:</label>
            <input
              className="custom-input"
              type="tel"
              id="phoneNumber"
              name="phoneNumber"
              placeholder="01xxxxxxxxx"
              onChange={handlePhoneNumberChange}
            />
          </div>

          <div className="form-group">
            <label className="address">Address:</label>
            <input
              className="custom-input_address"
              type="text"
              id="address"
              name="address"
              placeholder="Enter your address"
              onChange={handleAddressChange}
            />
            
          </div>

          <div>
            {passwordError && <span className="password-error">{passwordError}</span>}
          </div>

          <button className="button_signup" type="submit">
            Sign Up
          </button>
        </form>
        {submittedData && ( 
          <div className="submitted-data">
            <h2>Submitted Data:</h2>
            <pre>{JSON.stringify(submittedData, null, 2)}</pre>
          </div>
        )}
        
      </div>
    </div>
  );
};

export default SignUp;
