import React, { useState, useEffect } from 'react';
import KidsmealsCard from './KidsmealsCard';

const Kidsmeals = () => {
  const [kidsmeals, setKidsmeals] = useState([]);

  useEffect(() => {
    fetch('https://xfood-ob2l.onrender.com/products?category=kidsmeals')
      .then((response) => response.json())
      .then((data) => setKidsmeals(data))
      .catch((error) => console.error('Error fetching data:', error));
  }, []); 

  return (
    <div id="kidsmeal" className="sandwich-container">
      <div className="offer-container">
        <img src="titleMeals2/kidsmeal.png" alt="Offer" className="kids-section" />
      </div>
      <div className="sandwich-list">
        {kidsmeals.map((kidsmeal, index) => (
          <KidsmealsCard key={index} {...kidsmeal} />
        ))}
      </div>
    </div>
  );
};

export default Kidsmeals;
