import React, { useState } from 'react';
import axios from 'axios'; 
import { useHistory } from 'react-router-dom'; 
import './Verification.css'

const Verification = () => {
  const [verificationCode, setVerificationCode] = useState('');
  const [verificationError, setVerificationError] = useState('');
  const history = useHistory(); 

  const handleVerificationChange = (e) => {
    setVerificationCode(e.target.value);
    setVerificationError('');
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!verificationCode) {
      setVerificationError('Please enter the verification code');
      return;
    }

    try {
      
      const response = await axios.post('https://xfood-ob2l.onrender.com/verify', { code: verificationCode },{withCredentials:true});

      
       
        console.log('Verification successful!');
        
        history.push('/Login');
      
        setVerificationError('Incorrect verification code. Please enter the correct code.');
      
    } catch (error) {
      console.error('Error occurred during verification:', error);
      setVerificationError('Enter the correct verification code ❌');
    }
  };

  return (
    <div className="login-container">
      <div className="login-box">
        <h4>Enter the code that was sent to your Email 📧</h4>
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="verificationCode">Verification Code:</label>
            <input
              className={`custom-input ${verificationError ? 'error' : ''}`}
              type="text"
              id="verificationCode"
              name="verificationCode"
              placeholder="Enter verification code"
              value={verificationCode}
              onChange={handleVerificationChange}
            />
            {verificationError && <span className="passwor-error">{verificationError}</span>}
          </div>
          <button className="button_login" type="submit">
            Verify
          </button>
        </form>
      </div>
    </div>
  );
};

export default Verification;
