import React from "react";
import offerimage from '../images/title_of_meals/offers.png';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';

const Offers = () => {
  const swiperStyles = {
    marginTop: "10px", 
  };

  const slideStyles = {
    width: "280px", // Set the width of each slide for larger images
    height: "300px", 
  };

  return (
    <div>
      <div  id="offer" className="offer-container">
        <img src={offerimage} alt="Offer" className="offer-section" />
      </div>
      <Swiper
        spaceBetween={-100}
        slidesPerView={3}
        loop={true} 
        onSlideChange={() => console.log('slide change')}
        onSwiper={(swiper) => console.log(swiper)}
        style={swiperStyles} 
        freeMode={true} 
        freeModeMomentumRatio={10} 
      >
        <SwiperSlide style={slideStyles}>
          <img src="offers/11.webp" alt="" className="image1"/>
        </SwiperSlide>
        <SwiperSlide style={slideStyles}>
          <img src="offers/22.webp" alt="" className= "image1"/>
        </SwiperSlide>
        <SwiperSlide style={slideStyles}>
          <img src="offers/33.webp" alt="" className="image1"/>
        </SwiperSlide>
        <SwiperSlide style={slideStyles}>
          <img src="offers/44.webp" alt="" className="image1"/>
        </SwiperSlide>
        <SwiperSlide style={slideStyles}>
          <img src="offers/55.webp" alt="" className="image1"/>
        </SwiperSlide>
        <SwiperSlide style={slideStyles}>
          <img src="offers/11.webp" alt="" className="image1"/>
        </SwiperSlide>
        <SwiperSlide style={slideStyles}>
          <img src="offers/44.webp" alt="" className="image1"/>
        </SwiperSlide>
      </Swiper>
    </div>
  );
};

export default Offers;
