import React, { useState, useEffect } from 'react';
import BurgerSANCard from './BurgerSANCard';

const BurgerSAN = () => {
  const [sandwiches, setSandwiches] = useState([]);

  useEffect(() => {
    
    fetch('https://xfood-ob2l.onrender.com/products?category=burger')
      .then((response) => response.json())
      .then((data) => setSandwiches(data))
      .catch((error) => console.error('Error fetching data:', error));
  }, []); 

  return (
    <div id="Burger" className="sandwich-container">
      <div className="offer-container">
        <img src="titleMeals2/burgesection.png" alt="Offer" className="burger-section" />
      </div>
      <div className="sandwich-list">
        {sandwiches.map((sandwich, index) => (
          <BurgerSANCard key={index} {...sandwich} />
        ))}
      </div>
    </div>
  );
};

export default BurgerSAN;
