import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch, useLocation, useHistory } from 'react-router-dom';
import ScaleLoader from 'react-spinners/ScaleLoader';
import Nav from './Nav';
import { LanguageProvider } from './LanguageContext';
import Offers from './Offers';
import BurgerSAN from './BurgerSAN';
import ChickenSAN from './ChickenSAN';
import Saus from './Saus';
import Appetizers from './Appetizers';
import Desserts from './Desserts';
import Drinks from './Drinks';
import SignUp from './SignUp';
import Login from './Login';
import Kidsmeals from './Kidsmeals';
import FooterPage from './Footer';
import Verification from './Verification';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCircleArrowUp } from '@fortawesome/free-solid-svg-icons';
import './Home.css';
import ProductPage from './ProductPage';
function Home() {
  const [refreshed, setRefreshed] = useState(false);
  const location = useLocation();
  const history = useHistory();
  const [loading, setLoading] = useState(true);
  const color = '#d73737';

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Simulating data loading with a setTimeout (example: fetching data from an API)
        await new Promise(resolve => setTimeout(resolve, 850));
        setLoading(false); // Set loading to false after data loads (replace this with your actual data fetching logic)
      } catch (error) {
        console.error('Error fetching data:', error);
        setLoading(false); // Set loading to false even on error
      }
    };

    fetchData(); // Trigger the data fetching process
  }, []);

  useEffect(() => {
    if (!refreshed && location.pathname === '/') {
      setRefreshed(true);
      history.replace('/'); // Replace the current URL with the same URL to trigger a reload immediately
    }
  }, [location.pathname, refreshed, history]);

  return (
    <Router>
      <LanguageProvider>
        <div className="App">
          <div className={`loading ${loading ? 'visible' : 'hidden'}`}>
            <ScaleLoader
              color={color}
              loading={loading}
              height={210}
              width={10}
              aria-label="Loading Spinner"
              data-testid="loader"
            />
          </div>

          <div className={`page-content ${loading ? 'hidden' : 'visible'}`}>
            <Nav />
            <div className="main-content">
              
              <Route path="/verification" component={Verification} />
              <Route path="/signup" component={SignUp} />
              <Route path="/login" component={Login} />
              
              
              
              <Offers />
              <BurgerSAN />
              <ChickenSAN />
              <Appetizers />
              <Saus />
              <Kidsmeals />
              <Desserts />
              <Drinks />
              <FooterPage />
            </div>
            <div className="scroll-to-top" onClick={() => window.scrollTo(0, 0)}>
              <FontAwesomeIcon icon={faCircleArrowUp} style={{ color: '#ffa528' }} />
            </div>
          </div>
        </div>
      </LanguageProvider>
    </Router>
  );
}

export default Home;
