import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './index.css';
import CartButton from './CartButton';

const BurgerSANCard = ({ _id, title, images, ingredients, price, price_per_unit }) => {
  const [refreshedOnce, setRefreshedOnce] = useState(false);

  const handleRefresh = () => {
    if (!refreshedOnce) {
      setRefreshedOnce(true);
      setTimeout(() => {
        window.location.reload(); // Reload the page after a short delay
      }, 100); // Adjust the delay as needed
    }
  };

  return (
    <div className="product-card" id={`burger-product-${_id}`}>
      <Link
        to={{
          pathname: `/product/${_id}`,
          state: {
            productId: _id,
            productName: title,
            productImage: images,
            productIngredients: ingredients,
            productPrice: price,
            unit: price_per_unit
          }
        }}
        className="link"
        onClick={handleRefresh} // Call handleRefresh function on link click
      >
        <img
          src={images[0].url}
          alt={title}
          className="animate__animated"
          onMouseEnter={(e) => e.target.classList.add('animate__pulse')}
          onMouseLeave={(e) => e.target.classList.remove('animate__pulse')}
        />
      </Link>
      <div className="content">
        <h2 style={{ fontSize: "19px" }}>{title}</h2>
        <p>Ingredients: {ingredients.join(', ')}</p>
        <p>Price: EGP {price}</p>
        <Link
          to={{
            pathname: `/product/${_id}`,
            state: {
              productId: _id,
              productName: title,
              productImage: images,
              productIngredients: ingredients,
              productPrice: price,
              unit: price_per_unit
            }
          }}
          className="order-link"
          onClick={handleRefresh} // Call handleRefresh function on link click
        >
          <button className='order-button'>
            <CartButton />
          </button>
        </Link>
      </div>
    </div>
  );
};

export default BurgerSANCard;
